require('./popups.scss')

//  淘宝返利导入订单成功
const rebate_complain_success = {
  options: {
    effect: 'scale',
    cssClass: 'rebate-complain-success',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: (successDesc) => {
    return `
      <img class="logo" src="//qianka.b0.upaiyun.com/images/9cdaf2bf12e15cb12d674afd13340d7b.png" alt="">
      <div class="title">导入订单成功</div>
      <div class="desc">返利订单 <span style="color: #3A3A3A;font-family: PingFangSC-Regular;">${successDesc.orderSN}</span> 导入成功，集分宝 <span style="color:#ea5a49;">${successDesc.score}</span>个 已经补发至您的账户</div>
    `
  }
}

//  淘宝返利导入订单失败
const rebate_complain_fail = {
  options: {
    effect: 'scale',
    cssClass: 'rebate-complain-fail',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: (failDesc) => {
    return `
      <img class="logo" src="//qianka.b0.upaiyun.com/images/41b54334fb45773cd7fa55e2d9ff7682.png" alt="">
      <div class="title">导入订单失败</div>
      <div class="desc">返利订单 <span style="color: #3A3A3A;font-family: PingFangSC-Regular;">${failDesc.orderSN}</span> 返利导入订单失败，理由：提交资料不足证明完成本任务</div>
    `
  }
}

const rebate_jump_to_taobao = {
  options: {
    effect: 'scale',
    cssClass: 'rebate-jump-to-taobao'
  },
  template: (start, remind) => {
    return `
      <img class="logo" src="//qianka.b0.upaiyun.com/images/7123f087f11b66449c47b2214516eb2e.png" alt="">
      <div class="desc">正在跳转淘宝官网（<span id="countDowm">${start}</span>s）</div>
      <div class="remind">${remind}</div>
    `
  }
}

const open_rebate_entrance = {
  options: {
    effect: 'scale',
    cssClass: 'open_rebate_entrance',
    showClose: true,
    buttons: [
      {text: '我要返利'}
    ]
  },
  template: () => {
    return `
      <div class="title">钱咖新玩法，<span>淘宝返利</span></div>
      <div class="title">淘宝天猫买就返，<span>最高36%</span></div>
      <div class="pic"><img src="//qianka.b0.upaiyun.com/images/f3182a4934fed9b8280e6fe61f84164c.png"></div>
    `
  }
}

// 提交导入订单成功弹层
const rebate_submit_apply_success = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general rebate_submit_apply_success',
    buttons: [
      {text: '返回'},
      {text: '立即查看'}
    ]
  },

  template: () => {
    return `
    <div class="title">提交成功</div>
    <div class="desc">
      订单比对将在3个工作日内处理完毕
    </div>
  `
  }
}

module.exports = {
  rebate_complain_success,
  rebate_complain_fail,
  rebate_jump_to_taobao,
  open_rebate_entrance,
  rebate_submit_apply_success
}



