<template>
  <div class="page" v-nav="{hideNavbar: true}">
     <AnimateHeader
      bg-color="#FFA70F"
      menu-bg-color="#FB940C"
      color="#FFFFFF"
      title="导入订单"
      :show-back-button="true"
      :on-back-button-click="back"
      :animate="false"
    >
    </AnimateHeader>
    <div v-if="isShowContent" class="page-content">
      <div class="desc">订单比对数据由&nbsp;<span class="c0">第三方商家</span>&nbsp;提供</div>
      <div>
        <div class="cb5">订单号</div>
        <div>
          <form>
            <input  ref="orderid_input" type="text" v-model="order_id" class="order-input" placeholder="可在淘宝客户端复制">
         </form>
        </div>
        <!-- <p>Message is: {{ isActive }}</p> -->
        <div>
          <div class="hint">以下操作及情况无返利</div>
          <p class="ite">1、您使用了非Safari浏览器</p>
          <p class="ite">2、您未通过钱咖返利购买商品</p>
          <p class="ite">3、购物时使用了红包、优惠券的部分</p>
          <p class="ite">4、聚划算商品</p>
          <p class="ite">5、虚拟商品（如游戏账号等）</p>
          <p class="ite">6、退货成功的商品</p>
          <p class="ite">7、淘宝因购物节等短期设置了不可返利的商品</p>
          <div><input type="button" @click="submitAppeal()" class="btn-submit" v-bind:class="{ activeBtn: isActive }" value="提交"></div>
        </div>
      </div>
    </div>

  </div>

</template>
<style lang="scss" scoped>
  .page-content {
    padding: 70px 16px;
    background-color: #fff;
    -webkit-touch-callout: default;
    -webkit-user-select: none;

    .desc {
      background-color: #FFF8EE;
      height: 40px;
      line-height: 40px;
      text-align: center;
      font-size: 12px;
      margin-bottom: 40px;
      font-family: PingFangSC-Regular;
      color: #3A3A3A;

      .c0 {
        color: #FB800C;
      }
    }

    .order-input {
      font-family: PingFangSC-Regular;
      width: 100%;
      border-bottom: 1px solid #dddddd;
      font-size: 24px;
      height: 36px;
      border-radius: 0;
      margin-bottom: 50px;
      color: #3A3A3A;
      -webkit-touch-callout: default;
    }

    .order-input::-webkit-input-placeholder {
      color: #dddddd;
      font-size: 24px;
      line-height: 35px;
    }

    .hint {
      font-family: PingFangSC-Regular;
      font-size: 16px;
      color: #3A3A3A;
      margin-bottom: 12px;
    }

    .ite {
      font-family: PingFangSC-Regular;
      font-size: 13px;
      color: #888888;
      letter-spacing: 1px;
      line-height: 22px;
      margin-bottom: 0px;
    }

    .btn-submit {
      font-family: PingFangSC-Medium;
      font-size: 13px;
      width: 172px;
      height: 40px;
      background: #DDDDDD;
      border-radius: 2px;
      color: #fff;
      border: none;
      display: block;
      margin: 40px auto 0;
    }

    .btn-submit.activeBtn {
      background: #FFA70F;
    }

    .cb5 {
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #B5B5B5;
    }
  }
</style>
<script>
import AnimateHeader from '@/widget/animate-header/Index.vue'
import { rebate_submit_apply_success } from '../popups.js'

export default{
  data () {
    return {
      order_id: '',
      isShow: true,
      isShowContent: false
    }
  },

  components: {
    AnimateHeader: AnimateHeader
  },

  computed: {
    isActive () {
      return this.order_id.length >= 14
    }
  },

  mounted () {
    setTimeout(() => {
      this.isShowContent = true
    }, 300)
  },

  methods: {
    back () {
      // let backRoute = this.$route.name.split('_')[0]
      $router.back('/rebate/orderlist/daifan')
    },

    autoFocus () {
      this.$refs.orderid_input.focus()
    },

    submitAppeal () {
      this.order_id = this.order_id.replace(/\s/g, '')
      if (!this.order_id || this.order_id.length < 14 || !/^[1-9]\d*$/.test(this.order_id)) {
        $toast.show('请输入有效的订单号 ')
        return
      }
      $loading.show('提交中...')
      $api.jifenbaoComplainSubmit(this.order_id)
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          this.order_id = ''
          $loading.hide()
          let popup = $popup.fromTemplate(
            rebate_submit_apply_success.template(payload),
            rebate_submit_apply_success.options
          )
          popup.show().then((buttonIndex) => {
            if (buttonIndex === 0) {
              location.href = './orderlist/daifan'
            }
            if (buttonIndex === 1) {
              location.href = './orderlist/shensu'
            }
          })
        })
    }
  }
}
</script>



